<template>
    <div v-show="loadData" class="bink-list">
        <div v-show="!hasbind" class="nobind-section">
            <div class="div-empty">
                <div class="empty-wapper">
                    <span class="iconfont icon-yinxingqia"></span>
                    <p class="empty-tip">您还未添加银行卡</p>
                </div>
            </div>
            <div class="div-add" @click="addCardHandle">
                <div class="add-wapper"><span class="add-word iconfont icon-jia"> 新增银行卡</span></div>
            </div> 
        </div>
        <div v-show="hasbind" class="hasbind-secton">
            <p class="bind-tip">点击更改卡号&修改密码</p>
            <div @click="lookCardHandle" class="card-container">
                <div class="card">
                    <img class="bank-pic" :src="bankpic"/>
                    <p class="password-no">{{passwordNo}}</p>
                    <p class="mark">已绑定</p>
                </div>
                <div class="right-arrow">
                    <span class="iconfont icon-jiantou"></span>
                </div>
            </div>
            <p v-show="showInput" class="input-tip"><span class="iconfont icon-tishi1"></span>请先输入6位提现密码</p>
            <div v-show="showInput" class="input-container">
                <div class="div-input">
                    <input @textInput="textInutCheck" class="password-text" type="password" ref="passwordText1" v-model="passwordTextValue" placeholder="请输入提现密码" >
                </div>
                <mt-button size="large" class="commit-btn" type="default" @click.native="commitHandle">提交</mt-button>
            </div>
        </div>
    </div>
</template>
<script>
import { mapState } from "vuex";
import md5 from 'js-md5';
import Vue from "vue";
import { Toast, Button } from "mint-ui";
import "mint-ui/lib/button/style.css";
import "mint-ui/lib/toast/style.css";
Vue.component(Button.name, Button);
Vue.component(Toast.name, Toast);

export default {
  name: "bank-list",
  data() {
    return {
        loadData:false,
      bankpic: "",
      hasbind: false,
      cardNo: "",
      passwordNo: "",
      accountname: "",
      showInput: false,
      passwordTextValue: "",
      bankcode:""
    };
  },
  created() {
    this.$http
      .post(this.path + "php/suncode/dealfuc.php", {
        fuc: "banklist",
        user: this.userid
      })
      .then(response => {
        if(!this.loadData) this.loadData=true;
        if (response.body == "") {
          this.hasbind = false;
        } else {
          this.hasbind = true;
          var respdata = response.body[0];
          this.cardNo = respdata.banknum;
          this.accountname = respdata.bankaccount;
          this.bankcode=respdata.code;
          var len = respdata.banknum.length;
          this.passwordNo =
            "**** **** **** **** " + respdata.banknum.substr(len - 3, 3);
          this.bankpic =
            "https://apimg.alipay.com/combo.png?d=cashier&t=" + respdata.code;
        }
      })
      .catch(response => {
        //请求出错处理
        console.log(response);
      });
  },
  computed: {
    ...mapState(["userid", "path"])
  },
  methods: {
    addCardHandle() {
      this.$router.push({ name: "bindBank" });
    },
    lookCardHandle() {
      if (this.showInput) return;
      this.showInput = true;
    },
    textInutCheck(event) {
      if (!/^[0-9]*$/.test(event.data)) {
        event.returnValue = false;
      }
      if (this.passwordTextValue.length >= 6) event.returnValue = false;
    },
    commitHandle() {
      if (!this.passwordTextValue) {
        this.showMsg("请输入提现密码");
        return;
      }
      this.$http
        .post(this.path + "php/suncode/dealfuc.php", { fuc: "checkbankpwd", user: this.userid, pwd: md5(this.passwordTextValue) })
        .then(response => {
          var status = response.body;
          if (status == "200") {
            this.$router.push({ name: "bindBank", params: { cardNo: this.cardNo, accountname: this.accountname,bankcode:this.bankcode }});
          } else {
            this.showMsg("密码错误");
          }
        });
    },
    showMsg: function(msg) {
      Toast({
        message: msg,
        iconClass: "iconfont icon-tishi1",
        duration: 1000
      });
    }
  }
};
</script>
<style lang="stylus">
.bink-list {
    display: flex;

    .nobind-section {
        flex: 1;
        display: flex;
        flex-direction: column;

        .div-empty {
            flex: 1;
            display: flex;
            flex-direction: column;
            text-align: center;
            justify-content: flex-end;

            .empty-wapper {
                .icon-yinxingqia {
                    display: inline-block;
                    font-size: 1.2rem;
                    color: #afafaf;
                    margin-bottom: 0.2rem;
                }

                .empty-tip {
                    font-size: 0.15rem;
                    color: #707070;
                    letter-spacing: 0.005rem;
                }
            }
        }

        .div-add {
            flex: 1;
            display: flex;
            flex-direction: column;
            text-align: center;
            justify-content: center;

            .add-wapper {
                margin-top: -0.6rem;

                .add-word {
                    font-size: 0.14rem;
                    line-height: 0.3rem;
                    display: inline-block;
                    color: #a2733d;
                    border: 1px #a2733d solid;
                    padding: 0 0.16rem;
                    letter-spacing: 0.01rem;
                }
            }
        }
    }

    .hasbind-secton {
        width: 100%;
        padding: 0 0.2rem;
        box-sizing: border-box;

        .bind-tip {
            font-size: 0.15rem;
            color: #707070;
            letter-spacing: 0.01rem;
            margin-top: 0.25rem;
            margin-bottom: 0.1rem;
            padding-left: 0.05rem;
        }

        .card-container {
            width: 100%;
            display: flex;
            justify-content: space-between;
            min-height: 1.2rem;

            .card {
                flex: 0 0 2.2rem;
                border: 1px #ccc solid;
                border-radius: 0.04rem;
                position: relative;
                box-shadow: 0px 1px 1px rgba(150, 150, 150, 0.2);

                .bank-pic {
                    margin-left: 0.1rem;
                    margin-top: 0.07rem;
                    height: 0.4rem;
                    width: auto;
                }

                .password-no {
                    font-size: 0.15rem;
                    color: #303030;
                    position: absolute;
                    right: 0.22rem;
                    top: 55%;
                    transform: translate(0, -50%);
                    letter-spacing: 0.01rem;
                }

                .mark{
                    font-size .12rem;
                    color: #999;
                    position: absolute;
                    right: 0.16rem;
                    bottom 0.08rem;
                    transform: translate(0, -50%);
                    letter-spacing: 0.01rem;
                }
            }

            .right-arrow {
                flex: 1;
                align-self: center;
                text-align: center;

                .icon-jiantou {
                    color: #999;
                    font-size: 0.34rem;
                }
            }
        }

        .input-tip {
            margin-top: 0.3rem;
            font-size: 0.15rem;
            color: #707070;
            letter-spacing: 0.01rem;
            margin-bottom: 0.12rem;

            .icon-tishi1 {
                font-size: 0.19rem;
                margin-right: 0.02rem;
            }
        }

        .input-container {
            display: flex;
            align-items: center;
            justify-content: space-between;
            height: 0.34rem;

            .div-input {
                width: 1.6rem;
                border-bottom: 1px #ddd solid;

                input {
                    width: 100%;
                    -webkit-appearance: none; // 去掉iphone背景阴影
                    border: none;
                    outline: none;
                    box-sizing: border-box;
                    -webkit-touch-callout: none;
                    color: #333;
                    padding-left: 0.05rem;
                    font-size: 0.14rem;
                    position: relative;
                    top: -0.01rem;
                    letter-spacing: 0.005rem;
                    line-height: 0.3rem;

                    &::-webkit-input-placeholder {
                        color: #ddd;
                        font-size: 0.14rem;
                        letter-spacing: 0.01rem;
                    }
                }
            }

            .commit-btn {
                flex: 0 0 1rem;
                background-color: #f8ef94;
                color: #595656;
                font-size: 0.13rem;
                letter-spacing: 0.04rem;
                height: 0.32rem;
                box-shadow: none;
            }
        }
    }
}
</style>


